. Thứ ba, có yếu tố nước ngoài trong truyện mang ý nghĩa rằng, cuộc chiến bảo vệ thiên nhiên không có biên giới, không ngôn ngữ, không sắc tộc. Họ có thể là cô bé người Mỹ học lớp một, cậu bé Việt Nam thích ở trong rừng, ông già tu sĩ ở ẩn trên núi hay một chuyên gia lâm nghiệp đã hy sinh mạng sống của mình vì một con voi. Họ cũng có thể là vị giáo sư già nào đó đến từ châu Âu có tấm tình yêu trong sáng với thiên nhiên và họ nói chung một thứ ngôn ngữ: Ngôn ngữ của tình yêu thương, ngôn ngữ của sự công bằng. Bìa tập truyện Mật ngữ rừng xanh vừa được tái bản "),_c('br'),_vm._v("Sau Mật ngữ rừng xanh, anh đang ấp ủ dự định sáng tác gì? "),_c('br'),_c('br'),_vm._v(" – Tôi đã viết thêm một vài đầu sách và sắp xuất bản vào dịp Hội sách TPHCM năm 2016 sắp tới. Đó là tập tản văn Xứ mộng hồn hoa do Phương Nam Book ấn hành, tập truyện ngắn Những gam màu hồi sinh ở NXB Trẻ và Sài Gòn cà phê ngọt đắng viết cùng tác giả trẻ Lưu Quang Minh cũng do NXB Trẻ ấn hành. Ngoài ra, tôi cũng đang gấp rút hoàn thành bản thảo tiểu thuyết đầu tiên được khởi viết cùng thời điểm bắt đầu với Mật ngữ rừng xanh… "),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_c('br'),_vm._v("  Sức khỏe của anh vốn không tốt lắm, nhưng sức làm việc như vậy thật đáng nể. Hiện nay cuộc sống đời thường của anh ra sao? "),_c('br'),_vm._v(" l Hiện tại tôi vẫn đang viết báo để mưu sinh, toàn tâm sáng tác và làm những việc mình ấp ủ. Tôi có nhiều mặt hạn chế về thể chất, dẫn đến nhiều việc mình còn thiếu sót nhưng nói chung đời sống của tôi ổn về mặt tư tưởng, điều đó giúp tôi vượt qua những khó khăn. "),_c('br'),_c('br'),_vm._v("  Từ trải nghiệm hiếm có của bản thân, anh nhìn nhận ra sao về vai trò của văn học đối với đời sống con người? "),_c('br'),_c('br'),_vm._v(" – Dường như mỗi người trong chúng ta vô tình bị cuốn vào guồng quay mỗi lúc một nhanh của đời sống, có lúc ta vui vẻ đón nhận và hào hứng với nó, nhưng có những lúc chúng ta chơi vơi không biết mình đang đi về đâu, làm gì hay sẽ phải làm gì, dù chúng ta đang hiến thân cho cái vòng quay ấy một cách mãnh liệt. Dưới cái nhìn của người viết trẻ, người trẻ thích đọc, tôi nhận thấy văn học là thứ truyền nạp một tinh thần trong trẻo, minh triết và không bị gò bó ở mọi khía cạnh đời sống con người. Dĩ nhiên văn chương, văn học đích thực sẽ hướng con người đến cái đẹp, khơi gợi cái đẹp trước những xói mòn của những tư tưởng hẹp hòi, ích kỷ đang đe dọa môi trường sống chúng ta. "),_c('br'),_c('br')])]),_c('div',{staticClass:"page",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.denketthuc()}}},[_c('KetThuc')],1)]):_vm._e()])])],1),_c('b-col',{attrs:{"cols":"2"}},[_c('RightCom')],1)],1)],1)
